import React, { PureComponent } from 'react'
import asyncImport from '../components/AsyncImport'

const Dashboard = asyncImport(() => import('../pages/NewDashboard'))
const ManagerDashboard = asyncImport(() => import('../pages/ManagerDashboard'))

const Index = (props: any) => {
  const { user } = props
  if (user.role === '0') {
    return (
      <ManagerDashboard {...props} skipLayout={true}></ManagerDashboard>
    )
  }
  return (
    <Dashboard {...props} skipLayout={true}></Dashboard>
  )
}

export default Index
